import { Component, OnInit } from '@angular/core';
import { of, Subject } from 'rxjs';
import { CsmDataService } from '../csm-data.service';
import {catchError, switchMap} from 'rxjs/operators'

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  page = 1;
  pageSize = 10;
  data: RewardQueryResult = {
    count: 0,
    rewards: [],
  };

  pageObs = new Subject<{page: number, pageSize: number}>();

  constructor(private csmDataService: CsmDataService) { }

  ngOnInit(): void {
    this.loadData()
  }

  private loadData() {
    this.pageObs
    .pipe(switchMap(({page, pageSize }) => {
      return this.csmDataService.loadData((page - 1) * pageSize, pageSize)
      .pipe(catchError((e: any) => {
        console.log('error loading data', e)
        return of({
          count: 100,
          rewards: [],
        });
      }))
    })).subscribe((data) => {
      this.data = data
    }, (e) => {
      console.log('failed to load data', e)
    })

    this.pageObs.next({
      page: this.page,
      pageSize: this.pageSize,
    })
  }

  public refreshData() {
    this.pageObs.next({
      page: this.page,
      pageSize: this.pageSize,
    })
  }

  public doPageChange() {
    this.page = 1;
    this.refreshData();
  }
}
