import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AdminSecretService } from './admin-secret.service';

@Injectable({
  providedIn: 'root'
})
export class CsmDataService {

  constructor(private adminSerectService: AdminSecretService,
    private httpClient: HttpClient) { }

  public loadData(start = 0, pageSize = 1000): Observable<RewardQueryResult> {
    return this.httpClient.post<RewardQueryResult>('https://csm-api.crust.run/csm/rewards', {
      secret: this.adminSerectService.getSecret(),
      start,
      pageSize,
    })
  }
}
